  private extractTextFromPdf(): string[] {
    const texts: string[] = [];
    
    if (!this.pdfTextStreamData?.pages) {
      return texts;
    }


    // ëª¨ë“  í˜ì´ì§€ì˜ í…ìŠ¤íŠ¸ ì¶”ì¶œ
    for (let pageIndex = 0; pageIndex < this.pdfTextStreamData.pages.length; pageIndex++) {
      const page: any = this.pdfTextStreamData.pages[pageIndex];
      
      if (page.sentences && page.sentences.length > 0) {
        // ë¬¸ì¥ ë‹¨ìœ„ë¡œ ì¶”ì¶œ (ì´ë¯¸ ì˜¬ë°”ë¥¸ ìˆœì„œë¡œ ì •ë ¬ë˜ì–´ ìˆìŒ)
        
        for (let i = 0; i < page.sentences.length; i++) {
          const sentence = page.sentences[i];
          if (sentence.text && sentence.text.trim().length > 0) {
            const sentenceText = sentence.text.trim();
            texts.push(sentenceText);
          }
        }
      } else if (page.textItems && page.textItems.length > 0) {
        // ë¬¸ì¥ ë°ì´í„°ê°€ ì—†ìœ¼ë©´ ê¸°ì¡´ ë°©ì‹ ì‚¬ìš©
        
        // ëª¨ë“  í…ìŠ¤íŠ¸ ì•„ì´í…œì„ ìœ„ì¹˜ë³„ë¡œ ì •ë ¬
        const allTextItems = page.textItems
          .filter((item: any) => item.text && item.text.trim().length > 0)
          .map((item: any) => ({
            text: item.text.trim(),
            x: item.position.x,
            y: item.position.y,
            width: item.position.width,
            height: item.position.height
          }))
          .sort((a: any, b: any) => {
            // Y ì¢Œí‘œë¡œ ë¨¼ì € ì •ë ¬ (ìœ„ì—ì„œ ì•„ë˜ë¡œ)
            const yDiff = b.y - a.y; // PDFëŠ” Yê°€ í´ìˆ˜ë¡ ìœ„ìª½
            if (Math.abs(yDiff) > 15) { // 15px ì´ìƒ ì°¨ì´ë‚˜ë©´ ë‹¤ë¥¸ ì¤„
              return yDiff;
            }
            // ê°™ì€ ì¤„ì´ë©´ X ì¢Œí‘œë¡œ ì •ë ¬ (ì™¼ìª½ì—ì„œ ì˜¤ë¥¸ìª½ìœ¼ë¡œ)
            return a.x - b.x;
          });

        allTextItems.forEach((item: any, index: number) => {
          console.log(`${index}: (${item.x.toFixed(1)}, ${item.y.toFixed(1)}) "${item.text}"`);
        });

        // ì¤„ ë‹¨ìœ„ë¡œ ê·¸ë£¹í•‘
        const lines: string[] = [];
        let currentLine = '';
        let currentY = allTextItems.length > 0 ? allTextItems[0].y : 0;
        let currentX = allTextItems.length > 0 ? allTextItems[0].x : 0;

        for (const item of allTextItems) {
          // ìƒˆë¡œìš´ ì¤„ì¸ì§€ í™•ì¸ (Y ì¢Œí‘œ ì°¨ì´ê°€ 15px ì´ìƒ)
          if (Math.abs(item.y - currentY) > 15) {
            if (currentLine.trim()) {
              lines.push(currentLine.trim());
              console.log(`ì™„ì„±ëœ ì¤„: "${currentLine.trim()}"`);
            }
            currentLine = item.text;
            currentY = item.y;
            currentX = item.x;
          } else {
            // ê°™ì€ ì¤„ì—ì„œ í…ìŠ¤íŠ¸ ê°„ê²© í™•ì¸
            const gap = item.x - (currentX + 50); // ëŒ€ëµì ì¸ ì´ì „ í…ìŠ¤íŠ¸ ë„ˆë¹„ ì¶”ì •
            if (gap > 20) { // 20px ì´ìƒ ê°„ê²©ì´ë©´ ê³µë°± ì¶”ê°€
              currentLine += ' ' + item.text;
            } else {
              currentLine += item.text;
            }
            currentX = item.x;
          }
        }

        // ë§ˆì§€ë§‰ ì¤„ ì¶”ê°€
        if (currentLine.trim()) {
          lines.push(currentLine.trim());
          console.log(`ë§ˆì§€ë§‰ ì¤„: "${currentLine.trim()}"`);
        }

        texts.push(...lines);
      } else {
      }
    }

    texts.forEach((line, index) => {
    });

    return texts;
  }

