import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AiFeaturesPanelComponent } from './components/ai-features-panel/ai-features-panel.component';
import { AiGlobalExpeditionComponent } from './components/ai-global-expedition/ai-global-expedition.component';
import { StartupPackage } from './components/startup-package/startup-package';
import { HomepageSolution } from './components/homepage-solution/homepage-solution';
import { AiGlobalExpeditionDetail } from './components/ai-global-expedition-detail/ai-global-expedition-detail';

const routes: Routes = [
  {
    path: '',
    component: AiGlobalExpeditionComponent
  },
  {
    path: 'features',
    component: AiFeaturesPanelComponent
  },
  {
    path: 'global-expedition',
    component: AiGlobalExpeditionComponent
  },
  {
    path: 'global-expedition-detail',
    component: AiGlobalExpeditionDetail
  },
  {
    path: 'startup-package',
    component: StartupPackage
  },
  {
    path: 'homepage-solution',
    component: HomepageSolution
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AiRoutingModule { }