import { Component, OnInit, AfterViewInit, OnDestroy, ElementRef } from '@angular/core';
import { ViewportScroller } from '@angular/common';
import { ThemeService } from '../../../../services/theme.service';

declare const gtag: Function;

@Component({
  selector: 'app-homepage-solution',
  standalone: false,
  templateUrl: './homepage-solution.html',
  styleUrl: './homepage-solution.scss'
})
export class HomepageSolution implements OnInit, AfterViewInit, OnDestroy {
  isDarkMode: boolean = false;

  constructor(
    private viewportScroller: ViewportScroller,
    private elementRef: ElementRef,
    private themeService: ThemeService
  ) {}

  ngOnInit(): void {
    // Subscribe to global theme
    this.themeService.currentTheme$.subscribe(theme => {
      this.isDarkMode = theme === 'dark';
    });
    
    // Scroll to top of the host element
    setTimeout(() => {
      const hostElement = this.elementRef.nativeElement;
      if (hostElement) {
        hostElement.scrollTop = 0;
      }
    }, 0);
  }

  ngAfterViewInit(): void {
    this.initScrollAnimations();
    
    // Final scroll to top after view is ready
    setTimeout(() => {
      const hostElement = this.elementRef.nativeElement;
      if (hostElement) {
        hostElement.scrollTop = 0;
      }
    }, 100);
  }

  ngOnDestroy(): void {
    // Cleanup if needed
  }

  scrollToSection(sectionId: string): void {
    const element = document.getElementById(sectionId);
    const hostElement = this.elementRef.nativeElement;
    
    if (element && hostElement) {
      const elementTop = element.offsetTop;
      hostElement.scrollTo({
        top: elementTop - 100, // 100px offset for better visibility
        behavior: 'smooth'
      });
    }
  }

  private initScrollAnimations(): void {
    const hostElement = this.elementRef.nativeElement;
    
    const observerOptions = {
      root: hostElement, // Use host element as scroll root
      threshold: 0.1,
      rootMargin: '0px 0px -50px 0px'
    };

    const observer = new IntersectionObserver((entries) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          entry.target.classList.add('visible');
        }
      });
    }, observerOptions);

    // Observe all fade-in elements within this component
    hostElement.querySelectorAll('.fade-in').forEach((el: Element) => {
      observer.observe(el);
    });

    // Add loading animation
    hostElement.querySelectorAll('.service-card, .step-card').forEach((card: Element, index: number) => {
      (card as HTMLElement).style.animationDelay = `${index * 0.2}s`;
    });
  }
}
